import React, { useState } from 'react';
import './App.css';
import SearchBar from "material-ui-search-bar";
import axios from 'axios';


function App() {
  const [searchValue, setSearchValue] = useState('');
  const onChangeSearch = query => setSearchValue(query);

  const [value, setValue] = useState('');
  

  return (
    <div className="App">
      <div className="container">
        <header className="App-header">
          <h1> Tinyfy </h1>
        </header>
        <div className="Subtitle-area">
          <h4>Shorten your URL!</h4>
        </div>
        <div className="Search-area">

          <SearchBar className='Search-bar' placeholder='Paste your url here' onChange={onChangeSearch} value={searchValue}
            onRequestSearch={() => {
              try {
                console.log(searchValue);
                const response = axios.post('http://localhost:9000/url', { url: searchValue });
                console.log('ðŸ‘‰ Returned data:', response.then((res) => {
                  console.log(res.data.slug);
                  setValue('http://localhost:9000/'+res.data.slug);
                  
                }));
              } catch (e) {
                console.log(`ðŸ˜± Axios request failed: ${searchValue}`);
              }
            }}
          />
          <card>
          <h3><a href="{value}" target="_blank">{value}</a></h3>
          </card>
        </div>
        

      </div>
    </div>

  );
}





export default App;
